.libraryCard {
    background: white;
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.2s ease-in-out;
  }
  
  .libraryCard:hover {
    transform: translateY(-5px);
  }
  
  .cardHeader {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-bottom: 10px;
  }

  .libraryLogo {
    width: 30px;
    height: 30px;
  }
  
  .libraryDescription {
    font-size: 14px;
    color: #666;
    margin-bottom: 10px;
  }
  
  .githubIcon {
    width: 25px;
    transition: opacity 0.2s ease-in-out;
  }
  
  .githubIcon:hover {
    opacity: 0.7;
  }