.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    margin-top: 30px;
}

h1 {
    font-size: 2em;
    margin-bottom: 20px;
    color: #0056b3;
    text-align: center;
}

h2 {
    font-size: 1.5em;
    margin-top: 20px;
    color: #333;
}

h3 {
    font-size: 1.2em;
    margin-top: 15px;
    color: #555;
}

p, ul {
    margin: 10px 0;
}

ul {
    padding-left: 20px;
}

ul li {
    margin-bottom: 5px;
}

strong {
    color: #0056b3;
}

a {
    color: #0056b3;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}