.navBar {
  background-color: rgba(0, 0, 0, 1);
  padding-top: 1em;
  padding-bottom: 1em;
  list-style-type: none;
  height: 80px;
}

.navBar a {
  text-decoration: none;
  padding-left: 1em;
  padding-right: 1em;
  color: rgba(255, 255, 255, 1);
}

.navBar li {
  display: inline-block;
  -webkit-text-stroke: 3px black;
}

.frame{
	width: 100%;
	height: 100%;
}
.frame ul{
	list-style: none;
	position: absolute;
}
.frame ul li{
	text-align: center;
	height: 40px;
	line-height: 40px;
	font-size: 40px;
	font-family: arial, sans-serif;
	font-weight: 600;
	-webkit-text-stroke: 3px black;
	color: white;
	margin-bottom: 10px;
	position: relative;
	cursor: pointer;
}
.frame ul.backlayer li{
	-webkit-text-stroke: 3px white;
	color: black;
	-webkit-clip-path: polygon(10% 10%, 89% 6%, 94% 91%, 10% 90%);
	clip-path: polygon(10% 10%, 89% 6%, 94% 91%, 10% 90%);
	animation: panbg .5s linear infinite;
	-webkit-animation: panbg .5s linear infinite;
	opacity: 0;
	-webkit-transition: ALL .2s;
    transition: all .2s;
}

.blue {
  background-color: skyblue;
}

.red {
  background-color: red;
}

.frame ul li:hover{
	opacity: 1;
}

@keyframes panbg{
	0%{
		-webkit-clip-path: polygon(10% 10%, 89% 6%, 94% 91%, 10% 90%);
		clip-path: polygon(20% 20%, 89% 6%, 94% 91%, 10% 90%);
	}
	50%{
		-webkit-clip-path: polygon(5% 5%, 93% 15%, 82% 87%, 2% 76%);
		clip-path: polygon(5% 5%, 93% 15%, 82% 87%, 2% 76%);
	}
	100%{
		-webkit-clip-path: polygon(10% 10%, 89% 6%, 94% 91%, 10% 90%);
		clip-path: polygon(20% 20%, 89% 6%, 94% 91%, 10% 90%);
	}
}

.hamburgerMenuContainer {
  display: none;
}

@media (max-width: 576px) {
  .navBar {
    display: none;
  }

  .hamburgerMenuContainer {
    display: block;
  }
}
