.hamburgerMenuContainer {
  width: 75px;
  height: 75px;
  position: absolute;
  top: 0;
}

.menuItem {
  background-color: rgba(0, 0, 0, 1);
  padding-top: 1em;
  padding-bottom: 1em;
  list-style-type: none;
  height: 40px;
  width: 100% !important;
  display: block;
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
  text-align: center;
  font-size: 2em;
}
