.gameCard {
    background: white;
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.2s ease-in-out;
    min-height: 200px;
  }
  
  .gameCard:hover {
    transform: translateY(-5px);
  }
  
  .gameImage {
    width: 100%;
    max-width: 300px;
    border-radius: 8px;
    transition: transform 0.2s ease-in-out;
  }
  
  .gameImage:hover {
    transform: scale(1.05);
  }
  
  .gameInfo {
    padding-top: 10px;
  }
  
  .gameTitle {
    font-size: 20px;
    font-weight: bold;
    margin: 10px 0;
    color: #333;
  }
  
  .gameDescription {
    font-size: 14px;
    color: #666;
    margin-bottom: 10px;
  }
  
  .extraLink a {
    font-size: 14px;
    color: #0073e6;
    text-decoration: none;
  }
  
  .extraLink a:hover {
    text-decoration: underline;
  }