.mainContent {
  position: relative;
  width: 80%;
  min-height: 80vh;
  border: solid rgba(50, 50, 50, 0.5) 2px;
  background-color: rgba(190, 190, 190, 0.5);
  border-radius: 1em;
  padding-left: 2em;
  padding-right: 2em;
  padding-top: 2em;
  padding-bottom: 2em;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2em;
}

.mainContent a {
  text-decoration: none;
}

.mainContent a:hover {
  color: red;
}

.divAvatar {
  position: relative;
  float: left;
  height : 468px;
  width: 145px;
}

.divText {
  padding-left: 10em;
}

.hidden {
  display: none;
}
